* {
  margin: 0;
  padding: 0;
}

.App {
  /* display: flex;
  align-items: center;
  justify-content: center; */
  width: 100%;
}

.Time span {
  font-family: -apple-system, BlinkMacSystemFont, "Neue Haas Grotesk Text Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 500;
  cursor:default;
  letter-spacing: 0.01em;
  font-size: 150px;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
  color: azure;
  /* color: #25272f */
}

body {
  background: #e0e0e0 ;
}

.smg {
  width: 100%
}

.item {
  display: flex;
  padding: 5px;
  margin: 5px;
  background: #fff;
}

.item:nth-child(1) {
  margin-top: 57px;
}

.Citem {
  margin-left: 5px;
  display: flex;
  flex-direction: column;
  flex: 8;
}

.Citem span {
  font-size: 10px;
}

.cop {
  justify-content: flex-end;
}

.Ritem {
  display: flex;
  flex: 3;
}

#excel-file {
  display: none;
}
.none {
  display: none;
}

.flex {
  background: rgba(238, 140, 123, 0.973);
  padding: 15px;
  display: flex;
  justify-content: space-between;
}

.top {
  position: fixed;
  top: 0;
  width: 100%;
}